import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/mall',
      name: 'index',
      meta: {index: 0},
      component: () => import('@/views/mall/index')
    },
    {
      path: '/mall/zcx',
      name: 'zcx',
      meta: {index: 1},
      component: () => import('@/views/mall/detail')
    },
    {
      path: '/mall/zzzx',
      name: 'zzzx',
      meta: {index: 1},
      component: () => import('@/views/mall/detail')
    },
    {
      path: '/mall/gtja',
      name: 'gtja',
      meta: {index: 1},
      component: () => import('@/views/mall/gtja')
    },
    {
      path: '/mall/gtja/interestRate',
      name: 'interestRate',
      meta: {index: 2},
      component: () => import('@/views/mall/gtja/interestRate')
    },
    {
      path: '/mall/gtja/creditDefaultSwap',
      name: 'creditDefaultSwap',
      meta: {index: 2},
      component: () => import('@/views/mall/gtja/creditDefaultSwap')
    },
    {
      path: '/mall/gtja/financialTimes',
      name: 'financialTimes',
      meta: {index: 2},
      component: () => import('@/views/mall/gtja/financialTimes')
    },
    {
      path: '/mall/gtja/financialTimes/:id/:title',
      name: 'financialTimesDetail',
      meta: {index: 3},
      component: () => import('@/views/mall/gtja/financialTimesDetail')
    },
    {
      path: '/mall/gtja/linked',
      name: 'linked',
      meta: {index: 3},
      component: () => import('@/views/mall/gtja/linked')
    },
    {
      path: '/mall/gtja/declare',
      name: 'declare',
      meta: {index: 3},
      component: () => import('@/views/mall/gtja/declare')
    },
    {
      path: '/mall/gtja/operation',
      name: 'operation',
      meta: {index: 3},
      component: () => import('@/views/mall/gtja/operation')
    },
    // 申请信息
    {
      path: '/mall/gtja/applyInformation',
      name: 'applyInformation',
      meta: {index: 3},
      component: () => import('@/views/mall/gtja/applyInformation')
    },
    // 申请中
    {
      path: '/mall/gtja/inReview',
      name: 'inReview',
      meta: {index: 4},
      component: () => import('@/views/mall/gtja/inReview')
    },
    // 申请记录
    {
      path: '/mall/gtja/applyRecord',
      name: 'applyRecord',
      meta: {index: 5},
      component: () => import('@/views/mall/gtja/applyRecord')
    },
    // 信息详情
    {
      path: '/mall/gtja/informationDetail',
      name: 'informationDetail',
      meta: {index: 6},
      component: () => import('@/views/mall/gtja/informationDetail')
    },
    // 业务介绍
    {
      path: '/mall/detail/businessIntroduction',
      name: 'businessIntroduction',
      meta: {index: 3},
      component: () => import('@/views/mall/detail/businessIntroduction')
    },
    // 评级报告
    {
      path: '/mall/detail/ratingReport',
      name: 'ratingReport',
      meta: {index: 3},
      component: () => import('@/views/mall/detail/ratingReport')
    },
    // 研究报告
    {
      path: '/mall/detail/researchReport',
      name: 'researchReport',
      meta: {index: 3},
      component: () => import('@/views/mall/detail/researchReport')
    },
    // 付费报告
    {
      path: '/mall/detail/payReport',
      name: 'payReport',
      meta: {index: 3},
      component: () => import('@/views/mall/detail/payReport')
    },
    // 沟通交流
    {
      path: '/mall/detail/communicate',
      name: 'communicate',
      meta: {index: 3},
      component: () => import('@/views/mall/detail/communicate')
    },
    {
      path: '/mall/yygl',
      name: 'yygl',
      meta: {index: 1},
      component: () => import('@/views/mall/yygl')
    },

    // 充电活动
    // 奖品列表
    {
      path: '/chargeActivity/prizeList',
      name: 'prizeList',
      meta: {index: 1},
      component: () => import('@/views/chargeActivity/prizeList')
    },
    // 集赞
    {
      path: '/chargeActivity/collectPrize',
      name: 'collectPrize',
      meta: {index: 2},
      component: () => import('@/views/chargeActivity/collectPrize')
    }
  ]
});



